@use './global/library.scss' as lib;

$tag-background: #F2F2F2;
$tag-border-radius: 30px;

.tags__title {
	font-weight: 600;
	font-size: 28px;
	line-height: 35px;
	margin-bottom: 20px;

	@media (max-width: lib.$tablet) {
		font-size: 24px;
		line-height: 32px;
		margin-bottom: 16px;
	}
}

.tags__list {
	@include lib.reset-list;
	display: flex;
	flex-wrap: wrap;
	gap: 10px;

	@media (max-width: lib.$tablet) {
		gap: 10px 5px;
	}
}

.tags__element {
	&:last-child {
		margin-right: 0;
	}
}

.tags__link {
	display: block;
	padding: 9px 20px 11px 20px;
	background-color: $tag-background;
	border-radius: $tag-border-radius;
	font-size: 16px;
	line-height: 20px;
	color: inherit;
	@include lib.tag-state;

	@media (max-width: lib.$tablet) {
		font-size: 14px;
		line-height: 18px;
		padding: 7px 15px 9px 15px;
	}

	@media (max-width: 580px) {
		font-size: 12px;
		line-height: 16px;
		padding: 5px 10px 6px 10px;
	}
}